<template>
  <div class="container">
    <div class="mobile-title">
      <navBar :titleText="'待办详情'"></navBar>
    </div>
    <div class="mobile-body">
      <p>目前具体进展情况</p>
      <div v-for="item in postsList" :key="item.id">
        <van-cell center :title="item.postDetails" :label="item.postPeople + ' ' + item.postTime" />
      </div>
      <img v-if="disableUse || bottomData.realEndTime" :src="posting">
      <img v-else :src="posting" @click="openPosting">

      <div class="item">
        <div class="img-frame" style="margin: 0.5rem 0rem" @click="downloadFiles(item, true)" v-for="item in problemImg" :key="item.id">
          <span>{{item.fileName}}</span>
          <img v-if="!disableUse && !bottomData.realEndTime" :src="closeAnnex" @click.stop="closeAnnexFun(item.id, item.parameterId)">
        </div>
        <van-uploader style="display: none" ref="vanUploader" accept="" :after-read="afterRead" />
        <img v-if="disableUse || bottomData.realEndTime" :src="add_annex">
        <img v-else :src="add_annex" @click="$refs.vanUploader.chooseFile()">
      </div>
      <hr color='#F3F3F3'>
      <p>基本信息</p>
      <van-cell center title="发布人" :label="info.issueUser" />
      <van-cell center title="发布时间" :label="info.issueTime" />
      <van-cell center title="状态" :label="info.status" />
      <van-cell center title="提醒提前量" :label="info.warnNumber + '天'" />
      <van-cell center title="标准分" :label="info.standardScore + '分'" />
      <van-cell center title="工作类型" :label="info.isCirculate" />
      <van-cell v-if="info.isCirculate == '循环性工作'" center title="完成起止时间" :label="info.wancheng" />
      <van-cell v-else center title="要求完成时间" :label="info.endTime" />
      <van-cell center title="循环周期" :label="info.cycleTime" />
      <van-cell center title="工作内容及要求" :label="info.details" />
      <hr color='#F3F3F3'>
      <p>分项任务</p>
      <van-cell v-for="item in subTasks" :key="item.id" center :title="item.itemTaskDescribe" :value="item.nodeType == 0 ? '开始节点' : item.nodeType == 1 ? '中间节点' : item.nodeType == 2 ? '结束节点' : '-'" :label="'要求完成时间：' + item.endTime" />
      <!-- <van-cell center title="一次性工作" value="中间节点" label="要求完成时间：2022.10.09" /> -->
      <hr color='#F3F3F3'>
      <p>责任单位</p>
      <img style="width: 100%" :src="dutyUnitImg">
      <van-cell v-for="item in dutyUnit" :key="item.deptId" center :title="item.deptName ? item.deptName : '-'">
        <template #label>
          <div v-for="ele in item.userNames" :key="ele.id">
            <span :style="{background: ele.workType == 'MANEUVERING' ? '#107e3e' : ele.workType == 'SAFETY' ? '#ce0c0c' : ele.workType == 'CRAFT' ? '#0a6ed1' : ele.workType == 'PRODUCTION' ? '#925ace' : '#9ca3af'}" class="color-button">
              {{ele.userName ? ele.userName : '-'}}
            </span> 
          </div>
        </template>
      </van-cell>
      <van-button disabled type="info">点击上传</van-button>
      <div class="img-frame" style="margin: 0.5rem 0rem" @click="downloadFiles(item, false)" v-for="item in echoFileList" :key="item.id">
        <span>{{item.fileName}}</span>
        <!-- <img v-if="!disableUse && !bottomData.realEndTime" :src="closeAnnex" @click.stop="closeAnnexFun(item.id, item.parameterId)"> -->
      </div>
    </div>
    <div class="float-button">
      <van-button :disabled='disableUse || bottomData.realEndTime' type="info" @click="confirmFun">
        <img :src="confirm">
        <span v-show="!btnTextVisible">确认</span>
      </van-button>
    </div>
    <!-- 发帖输入框 start -->
    <van-popup
    v-model="show"
    position="bottom"
    :style="{ height: '32%' }"
    >
    <div class="input-box">
      <p>{{inputText}}</p>
      <textarea v-model="description" cols="30" rows="5" placeholder="请输入"></textarea>
      <div class="button-box">
        <van-button type="default" @click="show = false">
          <span>取消</span>
        </van-button>
        <van-button type="info" @click="postingConfirm">
          <span>确认</span>
        </van-button>
      </div>
    </div>
    </van-popup>
    <!-- 发帖输入框 end -->
  </div>
</template>

<script>
import { Toast } from 'vant'
import navBar from '@/components/navBar.vue';
import { ImagePreview } from 'vant';
import dayjs from 'dayjs';

export default {
  name: 'WaitDealtConfirm',
  components: {
    navBar,
  },
  data() {
    return {
      show: false,
      btnTextVisible: false,
      disableUse: false,
      problemImg: [],
      pictureId: [],
      info: {},
      postsList: [],  //  发帖的信息
      subTasks: [], //  分项任务
      dutyUnit: [], //  责任单位
      echoFileList: [],
      description: '',
      bottomData: {},
      taskUnitId: '',
      taskId: '',
      deptId: '',
      postPeople: '',
      inputText: '',

      posting: require('@/assets/waitDealtSvg/posting.svg'),
      closeAnnex: require('@/assets/waitDealtSvg/closeAnnex.svg'),
      add_annex: require('@/assets/waitDealtSvg/add_annex.svg'),
      confirm: require('@/assets/waitDealtSvg/confirm.svg'),
      dutyUnitImg: require('@/assets/waitDealtSvg/dutyUnitImg.svg'),
    }
  },
  methods: {
    changeClose() {

    },
    openPosting() {
      this.show = true;
      this.inputText = '';
    },
    postingConfirm() {
      debugger
      // console.log(dayjs(new Date()).format('YYYY-MM-DD'))
      if ((this.description == '' || this.description == null) && this.inputText == '') {
        alert('请输入帖子内容');
        return;
      }
      this.show = false;
      if (this.inputText == '') { //  发帖确认
        this.$postHttp(`${this.$baseUrl}/OS-PM/overhaulManagement/${window.localStorage.getItem('versionId')}/postMessage`, 
          {
            postDetails: this.description,
            postPeople: this.postPeople,
            postTime: dayjs(new Date()).format('YYYY-MM-DD HH:mm:ss'),
            taskUnitId: this.taskUnitId,
          }
        ).then(res=> {
          console.log('postMessage', res)
          this.getPostsList();
          this.description = ''
        }).catch(err=> {
  
        })
      } else {  //  确认
        const obj = {
          identifyPeople: JSON.parse(window.localStorage.getItem('user')).userName,
          identifyPeopleId: JSON.parse(window.localStorage.getItem('user')).userId,
          realEndTime: "",
          id: this.taskUnitId,
          taskId: this.taskId,
          deptId: this.deptId,
        };
        obj.realEndTime = dayjs(new Date()).format('YYYY-MM-DD');
        obj.confirmDescription = this.description;
        console.log('obj', obj)
        const json = JSON.stringify(obj);
        const blob = new Blob([json], {
          type: "application/json",
        });
        const data = new FormData();
        data.append("taskUnit", blob);
        this.pictureId.forEach((item) => {
          data.append("files", item);
        });

        this.$postHttp(`${this.$baseUrl}/OS-PM/overhaulManagement/${window.localStorage.getItem('versionId')}/taskFeedBack`, data).then(res=> {
          console.log('taskFeedBack', res)
          if (res.status == 200) {
            this.disableUse = true;
            Toast.success('保存成功！');
          }
        }).catch(err=> {

        })

      }
    },
    showImg(imgPath) {
      ImagePreview([imgPath]);
    },
    downloadFiles(file, state) {
      console.log('downloadFiles', file)
      if (state) {
        if (!this.disableUse && !this.bottomData.realEndTime) {
          alert('保存后可以下载');
          return; 
        }
      }
      let isDownload = confirm('是否要下载' + ' ' + file.fileName + '?');
      if (isDownload) {
        this.uploadFile(file);
      }
    },

    closeAnnexFun(val, parameterId) {
      debugger
      this.problemImg = this.problemImg.filter(item=> {return val != item.id});
      this.pictureId = this.pictureId.filter(item=> {return parameterId != item.lastModified});
      console.log('problemImg', this.problemImg, 'pictureId', this.pictureId)
    },
    confirmFun() {
      if (this.disableUse) {
        return;
      }
      this.show = true;
      this.inputText = '确认描述';
      this.description = '';
    },
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      debugger
      console.log('afterRead', file);
      this.pictureId.push(file.file);

      // return; //  看pc端上传附件没有走接口，所以不走下面这段


      function dataURLtoFileFun(dataurl, filename) {  //  转二进制
        // console.log('dataurl', dataurl, 'filename', filename)
        // 将base64转换为文件，dataurl为base64字符串，filename为文件名（必须带后缀名，如.jpg,.png）
        let arr = dataurl.split(",");
        let mime = arr[0].match(/:(.*?);/)[1];
        let bstr = atob(arr[1]);
        let n = bstr.length;
        let u8arr = new Uint8Array(n);
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n);
        }
        return new File([u8arr], filename, { type: mime });
      }
      const fromData = new FormData();  //  需要传formData类型
      fromData.append('uploadFile', dataURLtoFileFun(file.content, file.file.name))
      console.log('fromData', fromData)
      // this.pictureId = [];
      // this. problemImg = [];
      this.$postHttp(`${this.$baseUrl}/CMS/file/upload`, 
      fromData
      ).then(res=> {
        console.log('upload', res)
        if (res.status == 200) {
          // this.pictureId.push(res.data.data.id);
          this.problemImg.push({fileName: res.data.data.fileName + '.' + res.data.data.fileType, imgPath: res.data.data.filePath, id: res.data.data.id, parameterId: file.file.lastModified})
          console.log('pictureId', this.pictureId, 'problemImg', this.problemImg)
        }
      }).catch(err=> {

      })
    },
    uploadFile(file) {  //  下载
      this.$getHttp(`${this.$baseUrl}/OS-PM/overhaulManagement/${window.localStorage.getItem('versionId')}/downLoadFile/${file.id}`, { responseType: 'blob' }).then(response => {
        console.log('response下载123', response, 'file', file)
        if (response.status == 200) {
          //提前加载plus对象  * 这个应该不用写，回头注释掉试试 *
          document.addEventListener('plusready',function () {},false);

          console.log('response.config.url', response.config.url)

          const dtask = plus.downloader.createDownload(response.config.url, {
                filename:"_downloads/"+file.fileName    //利用保存路径，实现下载文件的重命名
              }, function ( d, status ) {
              console.log('createDownload2',d, status)
                  if (status == 200) { // 下载成功    
                  //下载成功,d.filename是文件在保存在本地的相对路径，使用下面的API可转为平台绝对路径
                var fileSaveUrl = plus.io.convertLocalFileSystemURL(d.filename);
                console.log(fileSaveUrl)
                //进行DOM操作
                  // $("#downloadImg").attr('src',fileSaveUrl);
                plus.runtime.openFile(d.filename);	   //选择软件打开文件
            } else {//下载失败    
              alert( "Download failed: " + status );
              plus.downloader.clear();        //清除下载任务
            }      
          })
          console.log('dtask', dtask)

          dtask.start();

          let prg = 0;  
          let showLoading = plus.nativeUI.showWaiting("正在下载");  
          dtask.addEventListener('statechanged', function(task, status) {  
            // 给下载任务设置一个监听 并根据状态  做操作  
            switch (task.state) {  
              case 1:  
                showLoading.setTitle("正在下载");  
                break;  
              case 3:  
                prg = parseInt((parseFloat(task.downloadedSize) / parseFloat(task.totalSize)) * 100);  
                showLoading.setTitle("  正在下载" + prg + "%  ");  
                break;  
              case 4:  
                  plus.nativeUI.closeWaiting();  
                //下载完成  
                break;  
            } 
          })
        }
    }).catch(() => {});
    },
    getPostsList() {
      this.$getHttp(`${this.$baseUrl}/OS-PM/overhaulManagement/${window.localStorage.getItem('versionId')}/selectMessage/${this.taskUnitId}`, ).then(res=> {
        console.log('selectMessage', res)
        if (res.status == 200) {
          this.postsList  = res.data;
        }
      })
    },
    async handleInterface(module) { //  处理共同接口的  检修专项排查、检修统筹计划、大检修规范化管理
      let res = {};
      res  = await this.$postHttp(`${this.$baseUrl}/OS-PM/overhaulManagement/${window.localStorage.getItem('versionId')}/selectWorkTask`, 
        {
          scene: module == '检修专项排查' ? '2' : module == '检修统筹计划' ? '0' : module == '大检修规范化管理' ? '1' : '',
          tag: "feedback",
        }
      );
      res.data.results.forEach(item=> {
        item.itemTasks.forEach(task=> {
          if (task.id == this.$route.query.number) {
            debugger
            this.info = item;
            this.subTasks.push(task);
            console.log('taskUnitList', task)
            this.bottomData = task.taskUnitList[0];
            this.taskUnitId = task.taskUnitList[0].id;
            this.taskId = task.id;
            this.deptId = task.taskUnitList[0].deptId;
            this.echoFileList = item.fileRecords;

            this.postPeople = task.taskUnitList[0].identifyPeople ? task.taskUnitList[0].identifyPeople : JSON.parse(window.localStorage.getItem('user')).userName;
            console.log('postPeople', this.postPeople)
            // 回显 ↓
            task.taskUnitList.forEach((item, index) => {
              debugger
              if (item.id === this.taskUnitId) {
                this.description = item.confirmDescription;
                if (item.fileRecords) {
                  this.problemImg = item.fileRecords;
                  this.problemImg.forEach((item) => {
                    item.fileName = item.fileName;
                  });
                  // console.log( this.fileList,'item.fileRecords');
                }
              }
            });

            let taskUnitList = item.itemTasks[0].taskUnitList;
            taskUnitList.forEach((e) => {
              let obj = {
                deptId: e.deptId,
                deptName: e.deptName,
                userNames: [],
                deptUserId: [],
                showUserNames: ''
              };
              if (e.userList) {
                e.userList.forEach((user) => {
                  obj.showUserNames += user.userName + ' ,'
                  let obj2 = {
                    id: user.id,
                    userName: user.userName,
                    workType: user.majorPosition,
                  };
                  obj.userNames.push(obj2);
                  obj.deptUserId.push(user.id + "-" + e.deptId);
                });
              }
              this.dutyUnit.push(obj);
            });

            console.log('subTasks', this.subTasks, 'dutyUnit', this.dutyUnit, 'problemImg', this.problemImg)
          }
        })
      })
      this.getPostsList();
      this.info.status = this.info.status == 1 ? "已发布" : "未发布";
      this.info.isCirculate = this.info.isCirculate == 1 ? "循环性工作" : "一次性工作";
      this.info.wancheng = this.info.startTime + " 至 " + this.info.endTime;
      console.log(this.$route.query.waitDealtName, res, 'this.info', this.info)
    },
  },
  mounted() {
    this.handleInterface(this.$route.query.waitDealtName);


    if (document.querySelector('.mobile-body')) {
      // debugger
      document.querySelector('.mobile-body').addEventListener('scroll', ()=> {
        // debugger
        // console.log(this)
        let timeoutHdr = null
        // 开始滚动时执行的代码
        this.btnTextVisible = true;
        window.clearTimeout(timeoutHdr)
        timeoutHdr = window.setTimeout(() => {
          // 滚动停止时执行的代码
          this.btnTextVisible = false;
        }, 101)
      });
    }
  },
}
</script>

<style lang='less' scoped>
  .img-frame {
    width: 100%;
    height: 3.125rem;
    border: 1px solid #808187 !important;
    border-radius: 0.5rem !important;
    display: flex;
    align-items: center;
    img {
      flex: 1;
      width: 1.5rem;
      height: 1.5rem;
      vertical-align: middle;
    }
    span {
      flex: 5;
      width: 100%;
      padding-left: 1rem;
      overflow: hidden;//溢出隐藏
      text-overflow: ellipsis;//属性值表示当对象内文本溢出时显示省略标记，省略标记插入的位置是最后一个字符。
      white-space: nowrap;//只保留一个空白，文本不会换行，会在在同一行上继续，直到遇到br标签为止
    }
  }
  .float-button {
    position: fixed;
    right: 1rem;
    bottom: 1rem;
    .van-button {
      border-radius: 1rem;
      font-size: 0.875rem;
      background: #0247fe;
      padding: 1rem;
      img {
        vertical-align: middle;
        margin-right: 0.25rem;
      }
    }
  }
  hr {
    margin: 2rem 0rem;
  }
  .input-box {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 1rem;
    padding-bottom: 0rem;
    p {
      margin-bottom: .2rem;
    }
    textarea {
      width: 100%; 
      resize:none;
      margin-top: -0.1rem;  
      border-radius: 0.5rem;
      border-color: #B4B4B4;
      box-sizing: border-box;
      padding: 1rem;
    }
    .button-box {
      display: flex;
      justify-content: space-between;
      .van-button {
        border-radius: 1.5rem;
      }
      .van-button--default {
        border: 0.0625rem solid #fff;
      }
    }
  }
  .color-button {
    color: #fff;
    padding: 0 5px;
    height: 24px;
    text-align: center;
    line-height: 24px;
    // margin: 0 10px;
    border-radius: 5px;
    display: inline-block;
  }
</style>